</template>
<script>

import SpaceWar from "@/components/post/SpaceWar.vue";
import { ref } from "vue";

// import router from "@/router/index"
export default {
    name: "TimerView",
    components: {
        SpaceWar: SpaceWar,
    },

    setup(){
        let confirm_content = ref("")
        let content = ref("")
        let is_start = ref("")
        let now_time = ref("")
        let start_time = ref("")
        let end_time = ref([])
        let start = ref("")
        let now = ref("")
        start_time.value = new Date()
        start.value = new Date().toLocaleTimeString()
        is_start.value = false
        // let end_list = ref("")

        setInterval(() => {
            now_time.value = new Date().toLocaleTimeString();
            now.value = new Date();
            // end_time.value = new Date(new Date().getTime() + timeLength.value * 60 * 1000).toLocaleTimeString();
        }, 10)


        const start_func =()=>{
            // time_length.value = 0
            is_start.value = true
            confirm_content.value = content.value
            
            start_time.value = new Date();
            end_time.value = []
            start.value = new Date().toLocaleTimeString();

        }
        const stop_func =()=>{
            confirm_content.value = ""
            is_start.value = false
        }
        const clear_func =()=>{
            end_time.value = []
        }

        const end_func =()=>{
            end_time.value.unshift(new Date())
        }

        const cal_length =(len)=>{
            let res = ""
            let seconds = (len / 1000)

            let minutes = Math.floor(seconds / 60)
            let hours = Math.floor(minutes / 60)

            minutes = minutes - hours * 60
            seconds = seconds - hours * 60 * 60 - minutes * 60
            

            if (hours >= 1){
                res += hours + " 小时"
            }
            if (minutes >= 1) {
                res += " " + minutes + " 分"
            }
            
            res += " " + seconds.toFixed(2) + " 秒"
            
            if (res == ""){
                res = " 0 秒"
            }
            return res

        }




        return {
            content,
            confirm_content,
            is_start,
            now,
            now_time,
            start_time,
            end_time,
            start,
            start_func,
            end_func,
            cal_length,
            stop_func,
            clear_func,

        }
    }


}

</script>
